'use client';

import React from 'react';
import { Typography, Space, Flex, Tag } from 'antd';
import { EnvironmentOutlined, CheckCircleFilled, InfoCircleOutlined } from '@ant-design/icons';
import { UserProfile } from '@/data/swipeData';
import { SwipeChoice } from './SwipeChoice';

const { Title, Text } = Typography;

interface SwipeCardProps {
  profile: UserProfile;
  style: React.CSSProperties;
  isTopCard: boolean;
  onMouseDown: (e: React.MouseEvent) => void;
  onTouchStart: (e: React.TouchEvent) => void;
  swipeX: number;
}

export function SwipeCard({ 
  profile, 
  style, 
  isTopCard, 
  onMouseDown, 
  onTouchStart, 
  swipeX 
}: SwipeCardProps) {
  const cardStyle: React.CSSProperties = {
    ...style,
    backgroundColor: '#ffffff',
    borderRadius: 16,
    overflow: 'hidden',
    backgroundImage: `url(${profile.images[0]})`,
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    position: 'relative'
  };

  const overlayStyle: React.CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'linear-gradient(to bottom, transparent 0%, transparent 60%, rgba(0,0,0,0.8) 100%)'
  };

  const contentStyle: React.CSSProperties = {
    position: 'absolute',
    bottom: 0,
    left: 0,
    right: 0,
    padding: 24,
    color: '#ffffff'
  };

  const verificationBadgeStyle: React.CSSProperties = {
    position: 'absolute',
    top: 16,
    right: 16
  };

  const infoButtonStyle: React.CSSProperties = {
    width: 40,
    height: 40,
    backgroundColor: 'rgba(255,255,255,0.2)',
    borderRadius: '50%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    backdropFilter: 'blur(4px)',
    marginLeft: 16,
    cursor: 'pointer'
  };

  const likeOpacity = Math.max(0, Math.min(1, swipeX / 150));
  const nopeOpacity = Math.max(0, Math.min(1, Math.abs(swipeX) / 150));

  // Handle ad type cards
  if (profile.type === 'ad') {
    const adContentStyle: React.CSSProperties = {
      position: 'absolute',
      bottom: 0,
      left: 0,
      right: 0,
      padding: 24,
      color: '#ffffff'
    };

    const sponsoredLabelStyle: React.CSSProperties = {
      position: 'absolute',
      top: 16,
      left: 16,
      backgroundColor: '#faad14',
      color: '#000000',
      padding: '4px 8px',
      borderRadius: 4,
      fontSize: 12,
      fontWeight: 700
    };

    const ctaButtonStyle: React.CSSProperties = {
      backgroundColor: '#ffffff',
      color: '#000000',
      padding: '12px 24px',
      borderRadius: 24,
      textAlign: 'center' as const,
      fontWeight: 700,
      border: 'none',
      cursor: 'pointer',
      width: '100%',
      marginTop: 16
    };

    return (
      <div
        style={cardStyle}
        onMouseDown={isTopCard ? onMouseDown : undefined}
        onTouchStart={isTopCard ? onTouchStart : undefined}
      >
        <div style={overlayStyle} />
        
        <Tag style={sponsoredLabelStyle}>
          SPONSORED
        </Tag>

        <div style={adContentStyle}>
          {profile.adData && (
            <>
              <Title level={2} style={{ 
                color: '#ffffff', 
                fontSize: 32, 
                fontWeight: 700, 
                margin: '0 0 8px 0' 
              }}>
                {profile.adData.title}
              </Title>
              <Text style={{ 
                color: 'rgba(255,255,255,0.9)', 
                fontSize: 16, 
                display: 'block', 
                marginBottom: 16 
              }}>
                {profile.adData.description}
              </Text>
              
              <div style={ctaButtonStyle}>
                {profile.adData.callToAction}
              </div>
            </>
          )}
        </div>

        {/* Like/Nope Indicators for ads */}
        {isTopCard && swipeX > 50 && (
          <div style={{
            position: 'absolute',
            top: 80,
            left: 32,
            opacity: likeOpacity
          }}>
            <SwipeChoice type="like" />
          </div>
        )}

        {isTopCard && swipeX < -50 && (
          <div style={{
            position: 'absolute',
            top: 80,
            right: 32,
            opacity: nopeOpacity
          }}>
            <SwipeChoice type="nope" />
          </div>
        )}
      </div>
    );
  }

  // Regular user profile card
  return (
    <div
      style={cardStyle}
      onMouseDown={isTopCard ? onMouseDown : undefined}
      onTouchStart={isTopCard ? onTouchStart : undefined}
    >
      <div style={overlayStyle} />

      {/* Verification Badge */}
      {profile.verified && (
        <div style={verificationBadgeStyle}>
          <CheckCircleFilled style={{ 
            color: '#1890ff', 
            fontSize: 32, 
            filter: 'drop-shadow(0 2px 4px rgba(0,0,0,0.3))' 
          }} />
        </div>
      )}

      {/* Profile Information */}
      <div style={contentStyle}>
        <Flex justify="space-between" align="end">
          <div style={{ flex: 1 }}>
            <Space align="baseline" size="small" style={{ marginBottom: 4 }}>
              <Title level={1} style={{ 
                color: '#ffffff', 
                fontSize: 48, 
                fontWeight: 700, 
                margin: 0, 
                lineHeight: 1 
              }}>
                {profile.firstName}
              </Title>
              <Title level={1} style={{ 
                color: '#ffffff', 
                fontSize: 48, 
                fontWeight: 300, 
                margin: 0, 
                lineHeight: 1 
              }}>
                {profile.age}
              </Title>
              {profile.verified && (
                <CheckCircleFilled style={{ color: '#1890ff', fontSize: 20 }} />
              )}
            </Space>
            
            <Space align="center" size="small" style={{ marginBottom: 8 }}>
              <EnvironmentOutlined style={{ 
                fontSize: 14, 
                color: 'rgba(255,255,255,0.9)' 
              }} />
              <Text style={{ 
                color: 'rgba(255,255,255,0.9)', 
                fontSize: 14, 
                fontWeight: 500 
              }}>
                {profile.distance} km away
              </Text>
            </Space>

            {profile.occupation && (
              <Text style={{ 
                color: 'rgba(255,255,255,0.8)', 
                fontSize: 14,
                display: 'block',
                marginBottom: 4
              }}>
                {profile.occupation}
              </Text>
            )}

            <Text style={{ 
              color: 'rgba(255,255,255,0.8)', 
              fontSize: 14,
              display: 'block'
            }}>
              {profile.location}
            </Text>
          </div>

          <div 
            style={infoButtonStyle}
            onClick={() => console.log('View profile:', profile.id)}
          >
            <InfoCircleOutlined style={{ color: '#ffffff', fontSize: 20 }} />
          </div>
        </Flex>
      </div>

      {/* Like/Nope Indicators */}
      {isTopCard && swipeX > 50 && (
        <div style={{
          position: 'absolute',
          top: 80,
          left: 32,
          opacity: likeOpacity
        }}>
          <SwipeChoice type="like" />
        </div>
      )}

      {isTopCard && swipeX < -50 && (
        <div style={{
          position: 'absolute',
          top: 80,
          right: 32,
          opacity: nopeOpacity
        }}>
          <SwipeChoice type="nope" />
        </div>
      )}
    </div>
  );
}