'use client';

import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { message, Layout, Flex } from 'antd';
import { motion, AnimatePresence } from 'framer-motion';
import { SwipeCardStack } from '@/components/swipe-stack/SwipeCardStack';
import { SwipeFooter } from '@/components/swipe-stack/SwipeFooter';
import { EmptyState } from '@/components/swipe-stack/EmptyState';
import { MatchModal } from '@/components/swipe-stack/MatchModal';
import { LoadingScreen } from '@/components/swipe-stack/LoadingScreen';
import { SwipeChoice } from '@/components/swipe-stack/SwipeChoice';
import { getUserProfiles, createSwipeRecord, UserProfile, SwipeDirection } from '@/data/swipeData';

const { Content } = Layout;

/** Configuration constants */
const SWIPE_THRESHOLD = 100;
const SWIPE_OUT_DISTANCE = 400;
const AD_FREQUENCY = 8;

/**
 * SwipeScreen Component
 * 
 * A Next.js/Ant Design implementation of a Tinder-style swipe interface featuring:
 * - Gesture-based swipe interactions (mouse/touch)
 * - Button-based swipe controls  
 * - Real-time profile matching
 * - Smooth animations and transitions
 * - Ad integration support
 * - Modular component architecture
 * 
 * @component
 */
const SwipePage: React.FC = () => {
  // Animation values for card movement and rotation
  const [swipeX, setSwipeX] = useState(0);
  const [swipeY, setSwipeY] = useState(0);
  const [rotation, setRotation] = useState(0);
  const [isDragging, setIsDragging] = useState(false);
  
  // State management
  const [profiles, setProfiles] = useState<UserProfile[]>([]);
  const [swipeCount, setSwipeCount] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const [matchedProfile, setMatchedProfile] = useState<UserProfile | null>(null);
  const [swipeHistory, setSwipeHistory] = useState<UserProfile[]>([]);
  
  // Refs for gesture handling
  const containerRef = useRef<HTMLDivElement>(null);
  const startPos = useRef({ x: 0, y: 0 });
  const animationFrameRef = useRef<number | null>(null);

  /**
   * Styles for the main container
   */
  const containerStyle: React.CSSProperties = {
    height: '100vh',
    backgroundColor: '#000000',
    position: 'relative',
    overflow: 'hidden'
  };

  const contentStyle: React.CSSProperties = {
    position: 'relative',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  };

  /**
   * Performance Optimizations
   * Memoized values to prevent unnecessary re-renders
   */
  const visibleProfiles = useMemo(() => 
    profiles.slice(0, 3), 
    [profiles]
  );

  const hasProfiles = useMemo(() => 
    profiles.length > 0, 
    [profiles.length]
  );

  const topCard = useMemo(() => 
    profiles[0], 
    [profiles]
  );

  /**
   * Initialize profiles on component mount
   */
  useEffect(() => {
    const initializeProfiles = async () => {
      try {
        setIsLoading(true);
        const userProfiles = await getUserProfiles();
        setProfiles(userProfiles);
      } catch (error) {
        message.error('Failed to load profiles');
      } finally {
        setIsLoading(false);
      }
    };

    initializeProfiles();
  }, []);

  /**
   * Auto-refresh profiles when stack becomes empty
   */
  useEffect(() => {
    if (profiles.length === 0 && !isLoading) {
      const refreshTimeout = setTimeout(async () => {
        try {
          const newProfiles = await getUserProfiles();
          setProfiles(newProfiles);
        } catch (error) {
          message.error('Failed to refresh profiles');
        }
      }, 1000);

      return () => clearTimeout(refreshTimeout);
    }
  }, [profiles.length, isLoading]);

  /**
   * Handle ad injection every AD_FREQUENCY swipes
   */
  const handleAdInjection = useCallback((swipeCount: number) => {
    if (swipeCount % AD_FREQUENCY === 0) {
      const adProfile: UserProfile = {
        id: `ad_${Date.now()}`,
        type: 'ad',
        firstName: 'Sponsored',
        lastName: 'Content',
        age: 0,
        images: ['https://images.unsplash.com/photo-1556742049-0cfed4f6a45d?w=400&h=600&fit=crop'],
        bio: 'Discover amazing products and services',
        location: 'Sponsored',
        distance: 0,
        verified: false,
        adData: {
          title: 'Premium Dating Features',
          description: 'Unlock unlimited likes and see who liked you!',
          callToAction: 'Get Premium Now'
        }
      };
      
      setProfiles(prev => [adProfile, ...prev]);
    }
  }, []);

  /**
   * Record swipe information
   */
  const recordSwipe = useCallback(async (profileId: string, direction: SwipeDirection) => {
    try {
      await createSwipeRecord(profileId, direction);
    } catch (error) {
      // Swipe recording failed - not critical for UX
      console.warn('Failed to record swipe:', error);
    }
  }, []);

  /**
   * Remove the top card from the stack
   */
  const removeTopCard = useCallback(() => {
    setProfiles(prev => {
      if (prev.length === 0) return [];
      
      const [removedCard, ...remaining] = prev;
      
      // Add to history for undo functionality
      setSwipeHistory(history => [removedCard, ...history.slice(0, 9)]);
      
      // Reset animation values
      setTimeout(() => {
        setSwipeX(0);
        setSwipeY(0);
        setRotation(0);
      }, 50);

      return remaining;
    });

    setSwipeCount(prev => {
      const nextCount = prev + 1;
      handleAdInjection(nextCount);
      return nextCount;
    });
  }, [handleAdInjection]);

  /**
   * Handle swipe completion
   */
  const handleSwipeComplete = useCallback(async (direction: SwipeDirection) => {
    const currentProfile = profiles[0];
    if (!currentProfile) return;

    // Record swipe for non-ad profiles
    if (currentProfile.type !== 'ad') {
      await recordSwipe(currentProfile.id, direction);
      
      // Check for match (30% probability for likes)
      if (direction === 'right' && Math.random() > 0.7) {
        setMatchedProfile(currentProfile);
        message.success({
          content: `üéâ It's a match with ${currentProfile.firstName}!`,
          duration: 3
        });
      } else if (direction === 'right') {
        message.success({
          content: `You liked ${currentProfile.firstName}! üíñ`,
          duration: 2
        });
      }
    } else {
      // Handle ad interaction
      if (direction === 'right') {
        message.info({
          content: 'Interested in this offer? Check it out!',
          duration: 2
        });
      }
    }

    removeTopCard();
  }, [profiles, recordSwipe, removeTopCard]);

  /**
   * Mouse/Touch gesture handlers for swipe functionality
   */
  const handleMouseDown = useCallback((e: React.MouseEvent) => {
    if (!hasProfiles) return;
    
    setIsDragging(true);
    startPos.current = { x: e.clientX, y: e.clientY };
    e.preventDefault();
  }, [hasProfiles]);

  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    if (!hasProfiles) return;
    
    setIsDragging(true);
    const touch = e.touches[0];
    startPos.current = { x: touch.clientX, y: touch.clientY };
  }, [hasProfiles]);

  const handleMouseMove = useCallback((e: MouseEvent) => {
    if (!isDragging) return;

    const deltaX = e.clientX - startPos.current.x;
    const deltaY = e.clientY - startPos.current.y;
    
    setSwipeX(deltaX);
    setSwipeY(deltaY);
    setRotation(deltaX * 0.1);
  }, [isDragging]);

  const handleTouchMove = useCallback((e: TouchEvent) => {
    if (!isDragging) return;

    const touch = e.touches[0];
    const deltaX = touch.clientX - startPos.current.x;
    const deltaY = touch.clientY - startPos.current.y;
    
    setSwipeX(deltaX);
    setSwipeY(deltaY);
    setRotation(deltaX * 0.1);
  }, [isDragging]);

  const handleDragEnd = useCallback(() => {
    if (!isDragging) return;

    setIsDragging(false);

    if (Math.abs(swipeX) > SWIPE_THRESHOLD) {
      // Complete the swipe
      const direction: SwipeDirection = swipeX > 0 ? 'right' : 'left';
      const targetX = swipeX > 0 ? SWIPE_OUT_DISTANCE : -SWIPE_OUT_DISTANCE;
      
      // Animate out
      setSwipeX(targetX);
      setRotation(targetX * 0.05);
      
      setTimeout(() => {
        handleSwipeComplete(direction);
      }, 300);
    } else {
      // Snap back to center
      setSwipeX(0);
      setSwipeY(0);
      setRotation(0);
    }
  }, [isDragging, swipeX, handleSwipeComplete]);

  /**
   * Global event listeners for mouse/touch events
   */
  useEffect(() => {
    if (isDragging) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleDragEnd);
      document.addEventListener('touchmove', handleTouchMove);
      document.addEventListener('touchend', handleDragEnd);
    }

    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleDragEnd);
      document.removeEventListener('touchmove', handleTouchMove);
      document.removeEventListener('touchend', handleDragEnd);
    };
  }, [isDragging, handleMouseMove, handleTouchMove, handleDragEnd]);

  /**
   * Handle button-based swipe choices
   */
  const handleChoice = useCallback((direction: number) => {
    if (!hasProfiles) return;

    const swipeDirection: SwipeDirection = direction > 0 ? 'right' : 'left';
    const animationDistance = direction * SWIPE_OUT_DISTANCE;

    // Animate button swipe
    setSwipeX(animationDistance);
    setRotation(animationDistance * 0.05);

    setTimeout(() => {
      handleSwipeComplete(swipeDirection);
    }, 300);
  }, [hasProfiles, handleSwipeComplete]);

  /**
   * Handle undo functionality
   */
  const handleUndo = useCallback(() => {
    if (swipeHistory.length === 0) return;

    const [lastProfile, ...remainingHistory] = swipeHistory;
    setProfiles(prev => [lastProfile, ...prev]);
    setSwipeHistory(remainingHistory);
    
    message.info({
      content: 'Undo successful! üîÑ',
      duration: 1
    });
  }, [swipeHistory]);

  /**
   * Handle refresh profiles
   */
  const handleRefresh = useCallback(async () => {
    try {
      setIsLoading(true);
      const newProfiles = await getUserProfiles();
      setProfiles(newProfiles);
      setSwipeHistory([]);
      message.success({
        content: 'New profiles loaded! üî•',
        duration: 2
      });
    } catch (error) {
      message.error('Failed to refresh profiles');
    } finally {
      setIsLoading(false);
    }
  }, []);

  /**
   * Conditional rendering based on loading and profile states
   */
  if (isLoading) {
    return <LoadingScreen />;
  }

  return (
    <Layout style={containerStyle}>
      <Content style={contentStyle}>
        {hasProfiles ? (
          <>
            {/* Card Stack */}
            <SwipeCardStack
              profiles={visibleProfiles}
              swipeX={swipeX}
              swipeY={swipeY}
              rotation={rotation}
              onMouseDown={handleMouseDown}
              onTouchStart={handleTouchStart}
            />

            {/* Footer Controls */}
            <SwipeFooter
              onChoice={handleChoice}
              onUndo={handleUndo}
              canUndo={swipeHistory.length > 0}
              topCard={topCard}
            />
          </>
        ) : (
          <EmptyState onRefresh={handleRefresh} />
        )}
      </Content>

      {/* Match Modal */}
      {matchedProfile && (
        <MatchModal
          profile={matchedProfile}
          onClose={() => setMatchedProfile(null)}
          onSendMessage={() => {
            setMatchedProfile(null);
            message.success('Opening chat...', 1);
          }}
        />
      )}
    </Layout>
  );
};

export default SwipePage;
