'use client';

import React from 'react';
import { Button, Typography, Space, Flex } from 'antd';
import { ReloadOutlined, FireFilled } from '@ant-design/icons';

const { Title, Paragraph } = Typography;

interface EmptyStateProps {
  onRefresh: () => void;
}

export function EmptyState({ onRefresh }: EmptyStateProps) {
  const containerStyle: React.CSSProperties = {
    textAlign: 'center' as const,
    maxWidth: 384,
    padding: 32
  };

  const iconContainerStyle: React.CSSProperties = {
    width: 96,
    height: 96,
    margin: '0 auto 24px auto',
    borderRadius: '50%',
    background: 'linear-gradient(135deg, #fce7f3 0%, #fde2e7 100%)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  };

  const primaryButtonStyle: React.CSSProperties = {
    background: 'linear-gradient(135deg, #ec4899 0%, #ef4444 100%)',
    border: 'none',
    borderRadius: 24,
    height: 48,
    padding: '0 32px',
    fontWeight: 600
  };

  return (
    <Flex justify="center" align="center" style={{ height: '100vh' }}>
      <div style={containerStyle}>
        <div style={iconContainerStyle}>
          <FireFilled style={{ color: '#ec4899', fontSize: 48 }} />
        </div>
        
        <Title level={2} style={{ 
          color: '#ffffff', 
          fontSize: 32, 
          fontWeight: 700, 
          marginBottom: 8 
        }}>
          No More Profiles!
        </Title>
        
        <Paragraph style={{ 
          color: 'rgba(255,255,255,0.8)', 
          fontSize: 16, 
          marginBottom: 24, 
          lineHeight: 1.6 
        }}>
          You've seen all available profiles in your area. 
          Check back later for fresh faces!
        </Paragraph>
        
        <Button
          type="primary"
          size="large"
          icon={<ReloadOutlined />}
          onClick={onRefresh}
          style={primaryButtonStyle}
        >
          Refresh Profiles
        </Button>
      </div>
    </Flex>
  );
}