'use client';

import React from 'react';
import { motion, PanInfo } from 'framer-motion';
import { SwipeCard } from './SwipeCard';
import { SwipeChoice } from './SwipeChoice';
import { UserProfile } from '@/data/swipeData';

interface SwipeCardStackProps {
  profiles: UserProfile[];
  onSwipeComplete: (direction: 'left' | 'right') => void;
  onSwipeProgress?: (x: number, y: number, rotation: number) => void;
}

const SWIPE_THRESHOLD = 100;
const SWIPE_POWER_THRESHOLD = 50000;

export function SwipeCardStack({
  profiles,
  onSwipeComplete,
  onSwipeProgress,
}: SwipeCardStackProps) {
  const containerStyle: React.CSSProperties = {
    position: 'relative',
    width: '100%',
    height: '100vh',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    perspective: '1000px',
  };

  const handleDragEnd = (event: any, info: PanInfo) => {
    const swipeVelocity = info.velocity.x;
    const swipeDistance = info.offset.x;
    const swipePower = Math.abs(swipeVelocity) * Math.abs(swipeDistance);

    if (swipePower > SWIPE_POWER_THRESHOLD || Math.abs(swipeDistance) > SWIPE_THRESHOLD) {
      const direction = swipeDistance > 0 ? 'right' : 'left';
      onSwipeComplete(direction);
    }
  };

  const handleDrag = (event: any, info: PanInfo) => {
    const rotation = info.offset.x * 0.1;
    onSwipeProgress?.(info.offset.x, info.offset.y, rotation);
  };

  if (profiles.length === 0) {
    return null;
  }

  return (
    <div style={containerStyle}>
      {profiles.slice(0, 3).map((profile, index) => {
        const isTopCard = index === 0;
        
        return (
          <motion.div
            key={`${profile.id}-${index}`}
            style={{
              position: 'absolute',
              width: 320,
              height: 520,
              borderRadius: 20,
              cursor: isTopCard ? 'grab' : 'default',
              zIndex: profiles.length - index,
            }}
            initial={false}
            animate={{
              scale: isTopCard ? 1 : 0.95 - index * 0.05,
              y: isTopCard ? 0 : index * 8,
              rotateY: isTopCard ? 0 : index * -2,
              opacity: 1,
              boxShadow: isTopCard 
                ? '0 8px 32px rgba(0, 0, 0, 0.3)' 
                : `0 ${4 + index * 2}px ${16 + index * 4}px rgba(0, 0, 0, ${0.2 - index * 0.05})`,
            }}
            transition={{
              type: "spring",
              stiffness: 300,
              damping: 30,
            }}
            drag={isTopCard ? "x" : false}
            dragConstraints={{ left: 0, right: 0 }}
            dragElastic={0.2}
            onDrag={isTopCard ? handleDrag : undefined}
            onDragEnd={isTopCard ? handleDragEnd : undefined}
            whileDrag={{
              cursor: 'grabbing',
              scale: 1.05,
              zIndex: 1000,
            }}
            exit={{
              x: 400,
              opacity: 0,
              scale: 0.8,
              transition: { duration: 0.3 }
            }}
          >
            <SwipeCard profile={profile} />
            
            {/* Choice indicator for top card - will be handled by parent component */}
          </motion.div>
        );
      })}
    </div>
  );
}