'use client';

import React, { useCallback } from 'react';
import { Button, Flex } from 'antd';
import { 
  UndoOutlined, 
  CloseOutlined, 
  HeartFilled, 
  StarFilled, 
  ThunderboltFilled,
  ReloadOutlined
} from '@ant-design/icons';
import { UserProfile } from '@/data/swipeData';

interface SwipeFooterProps {
  onChoice: (choice: number) => void;
  onUndo: () => void;
  canUndo: boolean;
  topCard?: UserProfile;
}

const COLORS = {
  like: '#52c41a',
  nope: '#ff4d4f', 
  star: '#1890ff',
  undo: '#8c8c8c',
  boost: '#722ed1'
};

export function SwipeFooter({ onChoice, onUndo, canUndo, topCard }: SwipeFooterProps) {
  const handleReload = useCallback(() => {
    console.log('Reload action');
  }, []);

  const handleFlash = useCallback(() => {
    console.log('Boost action');
  }, []);

  const handleStar = useCallback(() => {
    console.log('Super like action');
    onChoice(1); // Treat as like for now
  }, [onChoice]);

  // Check if current top card is an ad
  const isAd = topCard?.type === 'ad';

  const containerStyle: React.CSSProperties = {
    position: 'absolute',
    bottom: 20,
    left: '50%',
    transform: 'translateX(-50%)',
    zIndex: 1000,
    background: 'linear-gradient(to top, rgba(0,0,0,0.3) 0%, transparent 100%)',
    padding: '16px 24px',
    borderRadius: 40,
    backdropFilter: 'blur(10px)'
  };

  const baseButtonStyle: React.CSSProperties = {
    borderRadius: '50%',
    boxShadow: '0 4px 16px rgba(0,0,0,0.2)',
    transition: 'all 0.2s ease',
    border: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  };

  const smallButtonStyle: React.CSSProperties = {
    ...baseButtonStyle,
    width: 48,
    height: 48
  };

  const largeButtonStyle: React.CSSProperties = {
    ...baseButtonStyle,
    width: 56,
    height: 56
  };

  return (
    <div style={containerStyle}>
      <Flex justify="center" align="center" gap="large">
        {/* Undo Button */}
        <Button
          style={{
            ...smallButtonStyle,
            backgroundColor: '#ffffff',
            opacity: canUndo ? 1 : 0.5,
            cursor: canUndo ? 'pointer' : 'not-allowed'
          }}
          onClick={onUndo}
          disabled={!canUndo}
          icon={<UndoOutlined style={{ color: COLORS.undo, fontSize: 18 }} />}
        />

        {/* Pass Button */}
        <Button
          style={{
            ...largeButtonStyle,
            backgroundColor: '#ffffff'
          }}
          onClick={() => onChoice(-1)}
          icon={<CloseOutlined style={{ color: COLORS.nope, fontSize: 24, fontWeight: 'bold' }} />}
        />

        {/* Super Like Button */}
        <Button
          style={{
            ...smallButtonStyle,
            backgroundColor: '#ffffff',
            opacity: isAd ? 0.5 : 1,
            cursor: isAd ? 'not-allowed' : 'pointer'
          }}
          onClick={handleStar}
          disabled={isAd}
          icon={<StarFilled style={{ color: COLORS.star, fontSize: 18 }} />}
        />

        {/* Like Button */}
        <Button
          style={{
            ...largeButtonStyle,
            backgroundColor: COLORS.like
          }}
          onClick={() => onChoice(1)}
          icon={<HeartFilled style={{ color: '#ffffff', fontSize: 24 }} />}
        />

        {/* Boost Button */}
        <Button
          style={{
            ...smallButtonStyle,
            backgroundColor: '#ffffff',
            opacity: isAd ? 0.5 : 1,
            cursor: isAd ? 'not-allowed' : 'pointer'
          }}
          onClick={handleFlash}
          disabled={isAd}
          icon={<ThunderboltFilled style={{ color: COLORS.boost, fontSize: 18 }} />}
        />
      </Flex>
    </div>
  );
}